// Import Vue
import Vue from 'vue'

// Import F7
import Framework7 from 'framework7'

// Import F7 Vue Plugin
import Framework7Vue from 'framework7-vue'

import framework7Icons from 'framework7-icons/css/framework7-icons.css'

// Import F7 iOS Theme Styles
import Framework7Theme from 'framework7/dist/css/framework7.ios.min.css'
import Framework7ThemeColors from 'framework7/dist/css/framework7.ios.colors.min.css'
/* OR for Material Theme:
import Framework7Theme from 'framework7/dist/css/framework7.material.min.css'
import Framework7ThemeColors from 'framework7/dist/css/framework7.material.colors.min.css'
*/

// Import App Custom Styles
import AppStyles from './css/app.css'

// Import Routes
import Routes from './routes.js'

// Import App Component
import App from './app'

// Init F7 Vue Plugin
Vue.use(Framework7Vue)

// As a plugin 
import VueMask from 'v-mask'
Vue.use(VueMask);
 
// As a directive 
import { VueMaskDirective } from 'v-mask'
Vue.directive('mask', VueMaskDirective);

// vue-resource 
import VueResource from 'vue-resource'
Vue.use(VueResource);





// Init App
new Vue({
  el: '#app',
  data: {
    // message: {
    //   "type": "fgfgfg",
    //   "action": "",
    //   "user": "",
    //   "text": "",
    //   "timestamp": ""
    // },
    startUrl : 'http://www.forum-nnov.ru/api/sensus/',
    deviceId : '',
    isAutch : true,//false,
    StartPege:'MainPage',//'AutchPage',
    user:{
      token:"",
      cart:"",
      name:"",
      rights:""
    },
    categoryPath: "",
    points: [],
    filter: {},
    filterChek: {},
    countElement: 0,
    page: 1,
    pageSize: 30

  },
  template: '<app/>',
  // Init Framework7 by passing parameters here
  framework7: {
    root: '#app',
    /* Uncomment to enable Material theme: */
    // material: true,
    routes: Routes,
    swipePanel: 'left',
    animateNavBackIcon:true,
    pushState: true,
    pushStateSeparator:'#page',
    modalButtonOk: 'Подвердить',
    modalButtonCancel: 'Отменить',
    smartSelectBackText: 'Назад',
    smartSelectPopupCloseText: 'Закрыть',
    smartSelectPickerCloseText: 'Закрыть',
    notificationTitle: 'SENSUS-территории',
    notificationHold: 2000,
    notificationCloseIcon: false,
    

  },
  // Register App Component
  components: {
    app: App
  },
  methods:{
    setDeviceId : function () {
      var deviceId;
      deviceId = localStorage.getItem('device_id');

      if(!deviceId) {
        deviceId = this.makeid();
        localStorage.setItem('device_id',deviceId);
      }

      this.deviceId = deviceId;

    },
    makeid: function () {
      var text = "";
      var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
      for( var i=0; i < 12; i++ )
        text += possible.charAt(Math.floor(Math.random() * possible.length));
      return text;
    },
    chekAutch : function () {
      if (localStorage.getItem("sensus_authorize_key")) {
        this.user = JSON.parse(localStorage.getItem("sensus_authorize_key"));
        this.isAutch = true;
        this.StartPege = 'MainPage';
      }
    }
  },
  created:function () {
    //this.chekAutch();
    this.setDeviceId();
  }
});



