export default [
  {
      path: '/tasks/',
      component: require('./pages/point-list.vue')
  },
  {
      path: '/point/',
      component: require('./pages/point-list.vue')
  },
  {
      path: '/detail/:pointId/',
      component: require('./pages/point-edit.vue')
  }
]


