<template>
	<f7-card class="main-page">
		<f7-card-header>
			<f7-grid no-gutter>
			  <f7-col width="90">Статистика точек</f7-col>
			  <f7-col width="10" style="text-align:right">
			  	<f7-link href=""><f7-icon f7="reload_round" color="red"></f7-icon></f7-link>
			  </f7-col>
			</f7-grid>
		</f7-card-header>
		<f7-card-content>
			<f7-list media-list>
			  <f7-list-item
			    link="/point/?page=1"
			    title="Обработанно">
			    <div class="item-text" slot="inner">{{pointReady}} точек</div>
			  	<f7-icon f7="check_round" color="green" slot="media"></f7-icon>
			  </f7-list-item>
			  <f7-list-item
			    link="/tasks/?page=1"
			    title="Необработанно">
			    <div class="item-text" slot="inner">{{pointNotReady}} точек</div>
			  	<f7-icon f7="delete_round" color="red" slot="media"></f7-icon>
			  </f7-list-item>
			</f7-list>
		</f7-card-content>
		<div class="card-footer">Выполнено {{pointDiff}}%</div>
	</f7-card>
</template>

<style>
	.main-page .card-header .row{width: 100%}
	.main-page .list-block .item-text{height: auto;}
</style>

<script>
export default {
  data: function(){
    return{
      pointReady: 350,
      pointNotReady: 830,
      pointDiff:0,
    }
  },
  methods:{
  	pointDifference: function(){
  		const pointAll = this.pointReady+this.pointNotReady
  		this.pointDiff = Math.round( this.pointReady*100/pointAll );
  	}
  },
  created:function(){
  	this.pointDifference();
  }
}
</script>


