export default {
  data: function(){
    return{
      point:{
        ID: "",
        CONTRAGENT: "",
        AKBPKB: "",
        KOD: "",
        YLICO: "",
        GOLOVNOYCONTRAGENT: "",
        OBLAST: "",
        OBLAST_CODE: "",
        GOROD: "",
        GOROD_CODE: "",
        ADRES: "",
        DOLZHNOST: "",
        KONAKTNOELICO: "",
        MOBPHONE: "",
        EMAIL: "",
        ADDCONTACTS: "",
        CASH: "",
        FORMATTT: "",
        TYPETT: "",
        AUTOPARK: "",
        POSTS: "",
        STORAGE_SPACE: "",
        SALE_SPACE: "",
        PLOSHADZALA: "",
        PLOSHADGAZ: "",
        KOLVOSKU: "",
        UDALENNOST: "",
        PRICHINAOTKAZA: "",
        SPECIALTY_TYPE: [],
        SPECIALTY_MANUF: [],
        SPECIALTY_PRODUCT: [],
        SUPPLIER: [],
        TRAILERS: [],
        SENSUS_FOTO: [],
        SEGMENT: "",
        ADD_MANAGER: "",
        EDIT_MANAGER: "",
        PROCESSED: "",
        OBLAST_SEGMENT: "",
        DISTRICT: "",
        SPECIALIZATION_BUSINES: "",
        MOTIVE_CLOSED: "",
        REGION: []
      },
      dataForm:{
        REGION: {},
        SUPPLIERS: [],
        TRAILERS: [],
        SPECIALTY: {
          TYPE: [],
          MANUF: [],
          PRODUCT: []
        },
        PREDPOLAGKLASS: [],
        POSITION: [],
        FORMAT: [],
        TYPETT: {},
        GEOGRAFI: [],
        YLICO: [],
        AKBPKB: [],
        ZHELANIEVOYTI: [],
        NALICHIEFORMATOBORUD: [],
        GOTOVZAMENOFORUD: [],
        ATTESTACJA: [],
        FORMAT_CHEKED: "",
        TYPE: [],
        CLASS: ""
      }
    }
  },

  methods:{
    pointSaved:function(){

      //в случае успеха(пока эмуляция)
      console.log("Отправка точки на сохранение", this.$route.params.pointId)
      this.$f7.addNotification({name:'pointSaved',message:"Контрагент успешно сохранен!",hold:1500})
      this.$f7.mainView.router.back();

      const options = {
        params: {
          device: this.$root.deviceId,
          key: this.$root.user.token,
        }
      };

      // this.$http.get(this.$root.startUrl+'pointsave/'+this.$route.params.pointId+'/', options).then(function(res){
      //   const data = JSON.parse(res.bodyText)
      //   //Проверяем результат в случае успеха сообщаем об успехе и возвращаем на страницу назад
      //   console.log(this.point);

      //   this.$f7.hideIndicator();
      //   return false;
      // }, function(err){

      //   console.log(err);
      //   this.$f7.hideIndicator();
      //   return false;
      // });

    },
    loadPoint: function(){
      this.$f7.showIndicator();

      const options = {
        params: {
          device: this.$root.deviceId,
          key: this.$root.user.token,
        }
      };

      this.$http.get(this.$root.startUrl+'detail/'+this.$route.params.pointId+'/', options).then(function(res){
        const data = JSON.parse(res.bodyText)
        //Добавляем точки к уже имеющимся
        this.point = data.point;
        this.dataForm = data.form;
        

        console.log(this.point);

        this.$f7.hideIndicator();
        return false;
      }, function(err){

        console.log(err);
        this.$f7.hideIndicator();
        return false;
      });

    }
  },
  created:function(){
    this.loadPoint();
    this.$f7.showToolbar("#toolbar-save");
  },
  destroyed:function(){
    this.$f7.hideToolbar("#toolbar-save");
  }
}
</script>




