<template>

    <f7-card>
      <f7-card-header>
        <div style="margin: 0 auto;">
          <img src="../assets/images/header_logo2.png" style="height:39px;width:auto;">
        </div>
      </f7-card-header>
      <f7-card-content class="auch__form">

            <f7-card>
              <f7-card-header>
                <div class="color-red" style="font-size: 20px;text-transform: uppercase;">Вход</div>
              </f7-card-header>
              <f7-card-content>
                <f7-list form>
                  <f7-list-item
                    media=""
                    v-show="phoneShow"
                    >
                    <f7-icon f7="phone" color="red" slot="media"></f7-icon>
                    <div class="item-input">
                      <input v-mask="'+7(###) ###-##-##'" type="tel" v-model="phone" slot="inner" placeholder="телефон"></input>  
                    </div>
                  </f7-list-item>
                  <f7-list-item
                    media=""
                    v-show="codeShow"
                    >
                    <f7-icon f7="lock" color="red" slot="media"></f7-icon>
                    <div class="item-input">
                      <input v-mask="'####'" type="tel" v-model="code" slot="inner" placeholder="1234"></input>  
                    </div>
                  </f7-list-item>
                </f7-list>
                
              </f7-card-content>
              <f7-card-content v-show="isValid">
                <p class="color-red">{{erText}}</p>
              </f7-card-content>
              <f7-card-content>
                <f7-button @click="actionAutch()" big color="red">{{nameBottom}}</f7-button>
              </f7-card-content>
            </f7-card>

      </f7-card-content>
    </f7-card>


</template>
<style>
  .auch__form> div.card-content-inner{padding: 5px}
</style>
<script>

export default {
  data: function(){
    return{
      phone: "",
      code: "",
      phoneShow:true,
      codeShow: false,
      isValid:false,
      erText:"Неверный формат телефона",
      nameBottom:"Войти"
    }
  },
  methods:{
    setShow: function() {
      //this.$f7.popup('#popup')
      this.phoneShow = !this.phoneShow;
      this.codeShow = !this.codeShow;
      this.nameBottom = "Подтвердить код"
    },
    actionAutch: function() {
      //отправляем телефон
      if(this.phoneShow && !this.codeShow){
        if(this.phone.replace(/[^0-9]/gim,'').length < 10){
          this.isValid = true;
          this.erText = "Неверный формат телефона";
          return false;
        }
        //отправляем запрос кода подтверждения
        this.queryAutch();
        return false;
      }
      //отправляем код подтверждения
      if(!this.phoneShow && this.codeShow){
        //валидация телефон
        if(this.phone.replace(/[^0-9]/gim,'').length < 10){
          this.isValid = true;
          this.erText = "Неверный формат телефона";
          //меняем вид окна на ввод телефона
          this.phoneShow = !this.phoneShow;
          this.codeShow = !this.codeShow;
          this.nameBottom = "Войти"
          return false;
        }
        //валидация кода подтверждения
        if(this.code.replace(/[^0-9]/gim,'').length < 4){
          this.isValid = true;
          this.erText = "Неверный формат - код подтверждения";
          return false;
        }
        //отправляем запрос на авторизацию
        this.queryСonfirm();
        return false;
      }
    },
    queryAutch:function(){

      const options = {
        params: {
          phone: this.phone.replace(/[^0-9]/gim,''),
          device: this.$root.deviceId,
          key:"",
        }
      };
      this.$http.get(this.$root.startUrl+'autch/?step=code&type=phone', options).then(function(res){
        const data = JSON.parse(res.bodyText)

        if(data.code == "ok" && typeof data.errors === "undefined"){
          //в случае успеха
          this.isValid = false;
          this.setShow();
        }

        if(typeof data.errors !== "undefined"){
          this.isValid = true;
          this.erText = data.errors;
        }

        return false;
      }, function(err){
        //ошибка срединения
        this.isValid = true;
        this.erText = "Ошибка соединения с сервером";
        return false;
      });
    },
    queryСonfirm:function(){

      const options = {
        params: {
          phone: this.phone.replace(/[^0-9]/gim,''),
          code: this.code.replace(/[^0-9]/gim,''),
          device: this.$root.deviceId,
          key:"",
        }
      };

      this.$http.get(this.$root.startUrl+'autch/?step=autch&type=phone', options).then(function(res){
        const data = JSON.parse(res.bodyText)

        if(typeof data.token !== "undefined" && typeof data.errors === "undefined"){
          //в случае успеха
          this.isValid = false;
          this.setAutch(data);
        }

        if(typeof data.errors !== "undefined"){
          this.isValid = true;
          this.erText = data.errors;
        }

        return false;

      }, function(err){
        //ошибка срединения
        this.isValid = true;
        this.erText = "Ошибка соединения с сервером";
        return false;
      });
    },
    setAutch:function(data){
      //обновляем глобальные данные о пользаке
      this.$root.user = {
        token: data.token || "",
        cart: data.cart || "",
        name: data.name || "",
        rights: data.rights || "",
      };

      //записываем в localStorage инфу о пользаке
      localStorage.setItem("sensus_authorize_key", JSON.stringify(this.$root.user));

      //переключаем главную страницу на основную
      this.$root.isAutch = true;
      this.$root.StartPege = 'MainPage';
    },
  }
}
</script>


