<template>

      <f7-view navbar-fixed v-if="selectOpen" layout="white" theme="red">
        <f7-pages>
          <f7-page>
						<f7-navbar>
						  <f7-nav-left>
						  	<f7-link @click="closeFilter()">Применить</f7-link>
						  </f7-nav-left>
						  <f7-nav-center>Фильтр</f7-nav-center>
						  <f7-nav-right>
						    <f7-link @click="closeSelect()">Закрыть</f7-link>
						  </f7-nav-right>
						  <f7-subnavbar sliding>
							  <f7-searchbar
							    cancel-link="Отмена"
							    search-list="#search-filter"
							    placeholder="Поиск"
							    :clear-button="true"

							  ></f7-searchbar>
							</f7-subnavbar>
						</f7-navbar>
<!-- 						<f7-block-title>
							Найдено: {{ $root.countElement}} точек
						</f7-block-title> -->
						<f7-list  id="search-filter">
						  <f7-list-item v-for="(sel, k) in selectParam" checkbox name="my-checkbox" value="1" :title="sel.VALUE" v-model="sel.CHECKED" v-if="!sel.DISABLED" @click="chekSelectFiltr(sel)"></f7-list-item>
						</f7-list>
          </f7-page>
        </f7-pages>
      </f7-view>

      <f7-view navbar-fixed v-else layout="white" theme="red">
        <f7-pages>
          <f7-page>
            <f7-navbar title="Фильтр">
              <f7-nav-right>
                <f7-link @click="closeFilter()">Закрыть</f7-link>
              </f7-nav-right>
            </f7-navbar>
        			<f7-list media-list>
			          <f7-list-item
				          v-for="(item, key) in $root.filter" v-if="!item.DISABLED"
				          link="#" @click="selektEdit(key)"
				          class="searchbar-ignore">
				          <div slot="inner-start" class="item-title-row"><div class="item-title">{{item.NAME}}</div></div>
				          <div v-if="item.CHECKED" class="item-text" slot="inner">{{item.CHECKED}} выбран</div>
								</f7-list-item>
							</f7-list>
			<!-- 				<pre>{{$root.filter}}</pre>
							<pre>{{$props.filterItems}}</pre> -->
          </f7-page>
        </f7-pages>
      </f7-view>
</template>

<style>
  .list-block .item-text{height: auto;}
</style>

<script>
import {convert} from '../utils';

	export default {
		props: ['filter'],

	  data: function(){
	    return{
	      filterItems: {},
	      categoryPath: "",
	      postParams: {
	        device: this.$root.deviceId,
	        key: this.$root.user.token,
	        set_filter: true,
	      },
	      selectOpen:false,
	      selectParam:{}
	    }
	  },
	  mounted: function(){


	  	this.$root.$on('filterOpenPopup', function (data) {

console.log("1", this.filterItems);

	  	// 	this.categoryPath = data.categoryPath;

	   //    const options = {
	   //      params: this.postParams
	   //    };

				// this.$f7.showIndicator();
	   //    this.$http.get(this.$root.startUrl+'filter'+this.categoryPath, options).then(function(res){
	   //      const data = JSON.parse(res.bodyText)
	   //      this.filterItems = data;
	   //      console.log("2", this.filterItems);
	   //      this.$f7.hideIndicator();
	   //      return false;
	   //    }, function(err){
	   //      console.log(err);
	   //      this.$f7.hideIndicator();
	   //      return false;
	   //    });
	  	})
	  },
	   methods:{
    	selektEdit: function(id){
    		this.selectOpen = true;
    		this.selectParam = this.$root.filter[id].VALUES;
      },
      chekSelectFiltr: function(data){
      	
      	if(typeof this.$root.filterChek[data.CONTROL_ID] !== "undefined"){
      		delete this.$root.filterChek[data.CONTROL_ID];
      	}else{
      		this.$root.filterChek[data.CONTROL_ID] = "Y";
      	}
      	this.filterLoad()
      	console.log("filterChek", this.$root.filterChek);
      	//this.$emit('testedEvent', data);
      	//this.$parent.$options.methods.tester();
      },
	     filterLoad: function(){
	      this.$f7.showIndicator();

	      const options = {
	        params: this.postParams
	      };

	      this.$http.get(this.$root.startUrl+'filter'+this.$root.categoryPath+'?'+convert(this.$root.filterChek), options).then(function(res){
	        const data = JSON.parse(res.bodyText)
	        this.$root.filter = data.rows;
	        this.$root.countElement = data.count;
	        console.log(this.filter);
	        this.$f7.hideIndicator();
	        return false;
	      }, function(err){
	        console.log(err);
	        this.$f7.hideIndicator();
	        return false;
	      });

	    },
	    closeSelect:function(){
	    	this.selectOpen = false;
	    },
	    closeFilter:function(){
	    	this.$f7.closeModal('#filter')
	    	this.selectOpen = false;
	    	console.log("closeFilter", this.$root.filterChek);
	    	this.load();
	    	return false;
	    },
    load: function(){
      this.$f7.showIndicator();

      this.$root.page = 1;

      const options = {
        params: {
        	device: this.$root.deviceId,
        	key: this.$root.user.token,
        	set_filter: true,
        	pageSize: this.$root.pageSize,
        	page: this.$root.page
        }
      };

      this.$http.get(this.$root.startUrl+'point'+this.$root.categoryPath+'?'+convert(this.$root.filterChek), options).then(function(res){
        const data = JSON.parse(res.bodyText)
        this.$root.points = [];
        this.$root.points = data.point;

        console.log(this.$root.points);
        this.$f7.hideIndicator();
        return false;
      }, function(err){
        console.log(err);
        this.$f7.hideIndicator();
        return false;
      });
    },
    },
		// methods:{
	 //    filterLoad2: function(){
	 //      this.$f7.mainView.showIndicator();

	 //      const options = {
	 //        params: this.postParams
	 //      };

	 //      this.$http.get(this.$root.startUrl+'filter'+this.categoryPath, options).then(function(res){
	 //        const data = JSON.parse(res.bodyText)
	 //        console.log(data);
	 //        this.$f7.mainView.hideIndicator();
	 //        return false;
	 //      }, function(err){
	 //        console.log(err);
	 //        this.$f7.mainView.hideIndicator();
	 //        return false;
	 //      });

	 //    },
		// },
	   created:function(){

	  	// if(typeof this.$route !== "undefined"){
	  	// 	console.log("typeof", typeof this.$route.path);
	  	// 	if(this.$route.path == '/point/' || this.$route.path == '/tasks/'){
	  	// 		this.filterLoad();
	  	// 	}
	  	// }
	  }
	}
</script>


